<script>

export default {
  name: 'Cart',
  props: ['product', 'variant'],
  data() {
    return {
      baseUrl: window.localStorage.getItem("baseUrl"),
      token: window.localStorage.getItem("token"),
      quantity: 1,
      subTotal: null,
      total: null,
      promotion: false
    }
  },
  filters: {
    formatPrice(value) {
    	return value.replace('.', ',');
    }
  },
  created() {
    console.log(this.product);
    this.updateCart();
  },
  methods: { 
    increaseQuantity() {
      if (this.variant.length) {
        if (this.variant.quantity > this.quantity) {
          this.quantity += 1;
          this.updateCart();
        }
      } else {
        if (this.product.quantity > this.quantity) {
          this.quantity += 1;
          this.updateCart();
        }
      }
    },
    decreaseQuantity() {
      if (this.quantity > 1) {
        this.quantity -= 1;

        this.updateCart();
      }
    },
    updateCart() {
      if (this.variant.length) {
        this.subTotal = this.variant.price * this.quantity;
      } else {
        this.subTotal = this.product.price * this.quantity;
      }

      this.subTotal = this.subTotal.toFixed(2);
      this.total = this.subTotal;
    },
    goCheckout() {
      this.$router.push({ name: 'Checkout', params: { quantity: this.quantity, product: this.product, variant: this.variant.length ? this.variant : null } });
    },
  }
};

</script>

